<template>
    <div class="register container">
        <div class="info">
            <Tile :display="true"></Tile>
            <Step step="1"></Step>
            <div class="form">
                <table>
                    <tr>
                        <td class="tile"><p>电话号码 <span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入11位手机号码" v-model="phone" @blur="checkPhone()">
                                <span class="fail" v-show="access === false">手机号码格式不正确！</span>
                                <img v-show="access" class="access" src="../assets/signUp-icon-correct.png" alt="">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>验证码<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入验证码" v-model="code">
                                <div v-if="disabled" class="getcode disabled">{{timeOut}}</div>
                                <div v-else class="getcode" @click="sendMsg">{{timeOut}}</div>
                            </label>
                        </td>
                    </tr>
                </table>
                <div class="accept">
                    <label for="accept" :class="{select:select}">
                        <input id="accept" v-model="select" type="checkbox">
                    </label>
                    我已阅读并接受 <a href="">《福分通平台用户协议》</a>
                </div>
                <div class="btn" @click="checkSms">
                    下一步
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Tile from '../components/title'
    import Step from '../components/step'
    import {mapState} from 'vuex'

    export default {
        name: "index",
        components: {
            Tile,
            Step
        },
        data() {
            return {
                select: true,//用户须知
                timeOut: '获取验证码',//验证码
                access: '',//手机号格式验证通过
                // msgSend:false,
                msgPass: false,//短信验证通过
                phone: '',
                code: '',
                disabled: false,//按钮不可操作
            }
        },
        created() {
        },
        computed: {
            ...mapState('register', ['register'])
        },
        methods: {
            checkPhone() {
                let phone = this.phone;
                if (phone.length === 0) {
                    this.access = '';
                    return
                }
                let testPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
                this.access = testPhone.test(phone)
            },
            sendMsg() {
                if (this.access === true) {
                    this.yum('api/verify/sendSms', {phone: this.phone}).then(
                        (res) => {
                            if (res.data.status === 200) {
                                this.MsgTimeOut()
                            }
                            else {
                                return false
                            }
                        }
                    )
                } else {
                    alert('验证手机号码')
                }
            },
            MsgTimeOut() {
                let time = 60;
                let _self = this;
                _self.disabled = true;
                let timer = setInterval(function () {
                    time -= 1;
                    _self.timeOut = `请等待${time}秒`;
                    if (time === 0) {
                        clearInterval(timer);
                        _self.disabled = false;
                        _self.timeOut = '获取验证码'
                    }
                }, 1000)
            },
            checkSms() {
                let form = {
                    phone: this.phone,
                    sms: this.code
                };
                for (let i in form) {
                    if (form[i] === '' || form[i].length === 0) {
                        alert('正确填写手机号或者验证码');
                        return
                    }
                }
                this.yum('api/verify/verifySms', form).then(
                    res => {
                        if (res.data.status === 200) {
                            this.msgPass = true;
                            this.nextStep()
                        }
                    }
                )
            },
            nextStep() {
                if (this.select === true && this.access === true && this.msgPass === true) {
                    let data = {
                        register: {
                            step: 2,
                            phone: this.phone
                        }
                    };
                    this.$store.dispatch('register/setRegister', data);
                    this.$router.push('/ni')
                }
            }
        }
    }
</script>
<style scoped lang="stylus">
    .info
        width 800px
        margin 50px auto 0
        padding 0 50px 50px
        background-color white
        .form
            width 550px
            margin 60px auto 0
            table
                width 520px
                border-collapse separate
                border-spacing 20px 25px
                tr
                    height 50px
                    line-height 50px
                    td
                        height 50px
                        line-height 50px
                        &.tile
                            width 90px
                            font-size 16px
                            color black
                            font-weight bold
                            p
                                height 50px
                                line-height 50px
                                text-align justify
                        &.input
                            width 420px
                            position relative
                            input
                                height 50px
                                line-height 50px
                                width 100%
                                padding 0 10px
                                outline none
                                border-radius 4px
                                border 1px solid #999
                                &:focus
                                    box-shadow 0 0 5px #0089ff
                                    border-color #0089ff
                            img.access
                                display: inline-block
                                position: absolute
                                right: -25px
                                top: 17px
                            span.fail
                                position absolute
                                display inline-block
                                height 16px
                                line-height 16px
                                font-size 12px
                                color red
                                bottom -20px
                                left 0
                                animation debounce 0.1s 3
                            @keyframes debounce {
                                from {
                                    bottom -22px
                                }
                                to {
                                    bottom -20px
                                }
                            }
            .accept
                padding-left 120px
                font-size 12px
                user-select none
                font-weight bold
                a
                    color #0089ff
                    font-weight bold
                label
                    display inline-block
                    width 16px
                    height 16px
                    border 1px solid #0089ff
                    vertical-align text-bottom
                    margin-right 12px
                    border-radius 3px
                    &.select
                        background-image url('../assets/signUp-icon-selected.png')
                        background-position center
                    input
                        display none
        .btn
            width 378px
            height 60px
            line-height 60px
            margin 40px 120px 0
            text-align center
            font-size 18px
            background-color #0089ff
            color white
            cursor pointer
            border-radius 6px
            &:hover
                background-color #33a0ff
        .getcode
            width 120px
            height 100%
            background-color #eff9ef
            position absolute
            top 0
            right 0
            border-radius: 0 4px 4px 0;
            border: solid 1px #999999
            text-align center
            cursor pointer
            user-select none
            &.disabled
                background-color #e1e1e1
                cursor not-allowed
</style>